body {
    scrollbar-width: none;
}
.row{
    margin-left: 20px;
    color: #fff;
}

.posters{
    display: flex;
    padding: 20px;
    overflow-y: hidden;
    overflow-x: scroll;
}

.posters::-webkit-scrollbar{
    display: none;
}

.poster{
    max-height: 250px;
    margin-right: 10px;
    cursor: pointer;

}
.poster:hover{
    transform: scale(1.1);
}

.smallPoster{
    max-height: 150px;
    margin-right: 10px;
    cursor: pointer;
}
.smallPoster:hover{
    transform: scale(1.1);
}